import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
    {
        path: '',
        redirectTo: 'payroll'
    },
    {
        path: 'payroll',
        loadChildren: () => import('./payroll/payroll.module').then(m => m.PayrollModule)
    },
    {
        path: 'thr',
        loadChildren: () => import('./thr/thr.module').then(m => m.ThrModule)
    },
    {
        path: 'payout',
        loadChildren: () => import('./payout/payout.module').then(m => m.PayoutModule)
    },
    {
        path: 'bonus',
        loadChildren: () => import('./bonus/bonus.module').then(m => m.BonusModule)
    },
    {
        path: 'onduty',
        loadChildren: () => import('./onduty/onduty.module').then(m => m.OndutyModule)
    },
    {
        path: 'pilot',
        loadChildren: () => import('./pilot/pilot.module').then(m => m.PilotModule)
    },

    {
        path: 'bulanan',
        loadChildren: () => import('./bulanan/bulanan.module').then(m => m.BulananModule)
    },
    {
        path: 'tahunan',
        loadChildren: () => import('./tahunan/tahunan.module').then(m => m.TahunanModule)
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class LaporanRoutingModule { }
